"use client";

import { useContext } from "react";
import styles from "@/components/ProductsContainer.module.css";
import Image from "next/image";
import { CartContext } from "@/contexts/CartContext"; // Import the CartContext
import add_icon from "@/app/images/add.svg";
import remove_icon from "@/app/images/remove.svg";

type ProductProps = {
  product: {
    id: number;
    name: string;
    price: number;
    product_picture: string;
  };
};

export default function ProductCard({ product }: ProductProps) {
  const { cart, addToCart, removeFromCart, getQuantity } = useContext(CartContext);

  // Get current quantity from the context
  const quantity = getQuantity(product.id);

  return (
    <div className={`bg-white p-4 rounded-lg ${styles["product-card"]}`}>
      <Image
        className="border rounded-lg"
        src={product.product_picture}
        alt={product.name}
        layout="responsive"
        width={200}
        height={200}
        unoptimized
      />
      <div className="flex justify-start flex-col items-start mt-1">
        <h2 className="text-lg font-semibold">{product.name}</h2>
        <span className="flex gap-2 items-center">
          <span>Price: </span>
          <p className="text-[--title-color] text-sm">${product.price}</p>
        </span>
      </div>
      <div className="flex justify-end mt-4">
        {quantity === 0 ? (
          <button
            className={`${styles["card-btn-style"]}`}
            onClick={() => addToCart(product)}
          >
            Add
          </button>
        ) : (
          <div className="flex justify-between gap-2">
            <button
              className={styles["card-control-btn-style"]}
              onClick={() => removeFromCart(product.id)}
            >
              <Image src={remove_icon} alt="Remove" width={20} height={20} />
            </button>
            <span className="font-semibold text-md">{quantity}</span>
            <button
              className={styles["card-control-btn-style"]}
              onClick={() => addToCart(product)}
            >
              <Image src={add_icon} alt="Add" width={20} height={20} />
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
